.card {
  position: relative;
}
.card img {
  width: 100%;
  display: block;
  border-radius: 6px;
  color: rgb(224, 110, 157);
}

/* front of card - the picture */
.card .front {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
}
.flipped .front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  transition-delay: 0.2s;
}

/* back of card - cover */
.card .back {
  size: 100px;
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .back {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  transition-delay: 0s;
}
